// src/App.js

import React, { useState, useRef, useEffect } from 'react';
import FlowDiagram from './components/FlowDiagram';
import { initialData } from './data';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

function App() {
  const [helpOpen, setHelpOpen] = useState(false);
  const [helpMarkdown, setHelpMarkdown] = useState('');
  const helpPopupRef = useRef(null);

  // Mitigations modal state
  const [mitigationsOpen, setMitigationsOpen] = useState(false);
  const [mitigationsMarkdown, setMitigationsMarkdown] = useState('');
  const mitigationsPopupRef = useRef(null);

  useEffect(() => {
    if (helpOpen) {
      fetch('/help.md')
        .then(res => res.text())
        .then(setHelpMarkdown)
        .catch(() => setHelpMarkdown('Help information not available.'));
    }
  }, [helpOpen]);

  useEffect(() => {
    if (mitigationsOpen) {
      fetch('/mitigations.md')
        .then(res => res.text())
        .then(setMitigationsMarkdown)
        .catch(() => setMitigationsMarkdown('Mitigations information not available.'));
    }
  }, [mitigationsOpen]);

  useEffect(() => {
    function handleClickOutside(event) {
      if (
        helpOpen &&
        helpPopupRef.current &&
        !helpPopupRef.current.contains(event.target)
      ) {
        setHelpOpen(false);
      }
      if (
        mitigationsOpen &&
        mitigationsPopupRef.current &&
        !mitigationsPopupRef.current.contains(event.target)
      ) {
        setMitigationsOpen(false);
      }
    }
    if (helpOpen || mitigationsOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [helpOpen, mitigationsOpen]);

  return (
    <div className="p-6 min-h-screen bg-gray-900 relative">
      {/* Help and Mitigations icons in top right */}
      <div className="absolute top-5 right-5 z-40 flex gap-2">
        <button
          className="p-2 rounded-full border border-green-300 bg-white hover:bg-gray-100 shadow"
          onClick={() => setMitigationsOpen(true)}
          aria-label="Mitigations"
          type="button"
        >
          {/* Shield Check icon (Heroicons outline) */}
          <svg className="h-6 w-6 text-green-700" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" d="M12 4.5l7 3v5.5c0 5.25-7 8-7 8s-7-2.75-7-8V7.5l7-3z" />
            <path strokeLinecap="round" strokeLinejoin="round" d="M9 12l2 2 4-4" />
          </svg>
        </button>
        <button
          className="p-2 rounded-full border border-gray-300 bg-white hover:bg-gray-100 shadow"
          onClick={() => setHelpOpen(true)}
          aria-label="Help"
          type="button"
        >
          {/* Outline help icon (Heroicons) */}
          <svg className="h-6 w-6 text-gray-700" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" d="M8 10a4 4 0 118 0c0 2-2 3-2 3m-2 4h.01M12 18h.01" />
            <circle cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="2" />
          </svg>
        </button>
      </div>
      {/* Help popup */}
      {helpOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-60 overflow-y-auto backdrop-blur-sm">
          <div
            ref={helpPopupRef}
            className="bg-gray-900 rounded-lg shadow-lg max-w-4xl w-full p-6 relative my-8 border border-white"
            style={{
              maxHeight: '90vh',
              overflowY: 'auto',
            }}
          >
            <button
              className="absolute top-2 right-2 text-gray-400 hover:text-gray-700 text-2xl"
              onClick={() => setHelpOpen(false)}
              aria-label="Close"
              type="button"
            >
              &times;
            </button>
            <div className="prose prose-invert prose-a:text-white prose-headings:text-white max-w-none mb-4 text-white">
              <ReactMarkdown remarkPlugins={[remarkGfm]}>{helpMarkdown}</ReactMarkdown>
            </div>
            <div className="flex gap-3 mt-8">
              <a
                href="https://www.linkedin.com/in/jasper-baes/"
                target="_blank"
                rel="noopener noreferrer"
                className="inline-flex items-center px-3 py-2 rounded bg-blue-600 text-white hover:bg-blue-700"
              >
                {/* LinkedIn icon */}
                <svg className="h-5 w-5 mr-2" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.761 0 5-2.239 5-5v-14c0-2.761-2.239-5-5-5zm-11 19h-3v-10h3v10zm-1.5-11.268c-.966 0-1.75-.784-1.75-1.75s.784-1.75 1.75-1.75 1.75.784 1.75 1.75-.784 1.75-1.75 1.75zm15.5 11.268h-3v-5.604c0-1.337-.026-3.063-1.867-3.063-1.868 0-2.154 1.459-2.154 2.967v5.7h-3v-10h2.881v1.367h.041c.401-.761 1.381-1.563 2.841-1.563 3.039 0 3.6 2.001 3.6 4.601v5.595z"/>
                </svg>
                My LinkedIn
              </a>
              <a
                href="https://jbaes.be"
                target="_blank"
                rel="noopener noreferrer"
                className="inline-flex items-center px-3 py-2 rounded bg-gray-800 text-white hover:bg-gray-900"
              >
                {/* Globe icon */}
                <svg className="h-5 w-5 mr-2" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
                  <circle cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="2" />
                  <path strokeLinecap="round" strokeLinejoin="round" d="M2 12h20M12 2a15.3 15.3 0 010 20M12 2a15.3 15.3 0 000 20" />
                </svg>
                My website
              </a>
              <a
                href="https://buymeacoffee.com/jasperbaes"
                target="_blank"
                rel="noopener noreferrer"
                className="inline-flex items-center px-3 py-2 rounded bg-yellow-400 text-gray-900 hover:bg-yellow-300 font-semibold"
              >
                {/* Coffee cup icon */}
                <svg className="h-5 w-5 mr-2" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M18 8a1 1 0 011 1v2a5 5 0 01-5 5H8a5 5 0 01-5-5V9a1 1 0 011-1h14zm-1 7a7 7 0 01-14 0h14zm2-7h2a1 1 0 011 1v2a3 3 0 01-3 3v-6z"/>
                </svg>
                Buy Me a Coffee
              </a>
            </div>
          </div>
        </div>
      )}
      {/* Mitigations popup */}
      {mitigationsOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-60 overflow-y-auto backdrop-blur-sm">
          <div
            ref={mitigationsPopupRef}
            className="bg-gray-900 rounded-lg shadow-lg max-w-4xl w-full p-6 relative my-8 border border-white"
            style={{
              maxHeight: '90vh',
              overflowY: 'auto',
            }}
          >
            <button
              className="absolute top-2 right-2 text-gray-400 hover:text-gray-700 text-2xl"
              onClick={() => setMitigationsOpen(false)}
              aria-label="Close"
              type="button"
            >
              &times;
            </button>
            <div className="prose prose-invert prose-a:text-white prose-headings:text-white max-w-none mb-4 text-white">
              <ReactMarkdown remarkPlugins={[remarkGfm]}>{mitigationsMarkdown}</ReactMarkdown>
            </div>
          </div>
        </div>
      )}
      <h1 className="text-3xl font-bold mb-0 text-center text-gray-100">Entra ID Attack Paths</h1>
      <div className="flex justify-center items-center mb-4">
        <span className="text-sm text-gray-400 text-center">
          made by{' '}
          <b
            style={{
              background: 'linear-gradient(90deg, #e92a67 0%, #2a8af6 100%)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              backgroundClip: 'text',
              textFillColor: 'transparent',
            }}
          >
            <a href="https://linkedin.com/in/jasper-baes" target="_blank" rel="noopener noreferrer">Jasper Baes</a>
          </b>
        </span>
      </div>
      <FlowDiagram data={initialData} />
    </div>
  );
}

export default App;
